import { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
  body {
    height: 100%;
    width: 100%;
    margin: 0;
  }

  .bordered {
    border: 1px solid;
  }

  .orange {
    border-color: orange;
  }

  .no-padding: {
    margin: 0px;
    padding: 0px;
    color: orange;
  }
`;